<template>
<section>
    <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset" IdentityColumn="ParentRelatedInfoId" :HeaderText="$t('AssetsGroup')"  :ListData="AssetGroupingList" :HeaderData="Headers" :widgets="widgets" :showCheckBox="true" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">

        <template v-slot:slotdata="{ data }">

            <template v-if="data.column.COLUMN_NAME == 'Status'">
                <td class="text-center single-action">
                    <div class="cstm-drop-btn">
                        <select v-model="data.row.StatusId" @change="DrpdownUpdataStatus(data.row.StatusId,data.row.ParentRelatedInfoId);" :class="data.row.StatusId==1001?'ddlupdatestatus w-80 btn-success' :'ddlupdatestatus w-80 btn-danger'" >
                            <option value="1001" selected="selected">Active</option>
                            <option value="1002">Inactive</option>
                        </select>
                    </div>
                </td>
            </template>

        </template>
    </tg-list>

</section>
</template>

<script>
import DataService from '../../service/DataService'
import Schema from '../Asset/AssetGroupingListSchema'

export default {

    data() {
        return {
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            AssetGroupingList: [],
            pageNum: 1,
            search: "",
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            PageSizeOption: null,
            StatusListForListing: [],
            DetalisData: [],
            Headers: [{
                    "COLUMN_NAME": "UniqueName",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Container'),
                    "DISPLAY_ORDER": 1,

                },
                {
                    "COLUMN_NAME": "ItemCode",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ItemCode'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "GroupAssets",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Items'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "LocationName",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Location'),
                    "DISPLAY_ORDER": 4,

                },
                {
                    "COLUMN_NAME": "CreatedBy",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('CreatedBy'),
                    "DISPLAY_ORDER": 4,

                },
                {
                    "COLUMN_NAME": "CreatedAt",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('CreatedAt'),
                    "DISPLAY_ORDER": 4,

                },
                {
                    "COLUMN_NAME": "Status",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: true
                    }

                },
            ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortBy: "",
            sortExp: "",

            PageSize: 10,
            pageNum: 1,
            locationId: '',
            locationfrom: '',
            searchcondition: '',
            CreateAt:'',

        }
    },
    async created() {
        var ths = this;

        Schema.bindleftCommonSearchdropdown(ths, "Asset", "Location");
        Schema.bindleftCommonSearchdropdown(ths, "Asset", "Locationto");
        await ths.FetchData();

    },
    methods: {
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];
        },
        async FetchData() {            
            var vm = this;
            var Params = `&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&${vm.searchCondition}`;
            await DataService.GetAssetGrouping(Params).then(response => {                
                if (response.data != null) {
                    console.log('resp', response.data)
                    if (response.data.length > 0) {                   
                        vm.AssetGroupingList = response.data;                        
                        vm.TotalRecords = response.data[0].TotalRecord;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                    } else {
                        vm.noRecord = true;
                        vm.IntransitListData = [];
                        vm.TotalRecords = 0
                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });
        },
        leftCommonSearch: function (value) {

            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "AddGroup":
                    this.AddAssetGroup();
                    break;
                case "UpdateActiveAddGroup":
                    this.UpdateActiveAddGroup();
                    break;
                case "UpdateInActiveAddGroup":
                    this.UpdateInActiveAddGroup();
                    break;
                case "DeleteContainer":
                    this.DeleteContainer();
                    break;
            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortBy == "") {
                    this.sortBy = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

        DrpdownUpdataStatus(StatusID,ID) {            
            var vm = this;
            var StatusId = StatusID
            var Id = ID;
            vm.confirmR(vm.$t('Are you sure to update this record?'), true, false, function (result) {              
                var url = `id=${Id}&statusId=${StatusId}`
                DataService.UpdateContainerStatus(url).then(
                    response => {
                        console.log('res', response);
                        vm.FetchData();
                    }
                )
            })

            vm.FetchData();

        },
        AddAssetGroup:function(){            
            this.$router.push({name:'AddProductContainer'});
        },
        UpdateActiveAddGroup:function(){            
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });

            if (val.length > 0) {
                vm.confirmR(vm.$t('Are you sure, you want to mark the selected status from Inactive to Active?'), true, false, function (result) {              
                    var url=`id=${val}&statusId=1001`
                    DataService.UpdateActiveInactiveAddGroup(url).then(response => {
                        if (response.data) {                            
                            vm.ShowAlert(vm.$t('StatusUpdated', [vm.$t('Group_Container')]), "Success", true, vm.$t('Alert'));
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');                           
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group_Container')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
        },
        UpdateInActiveAddGroup:function(){            
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });

            if (val.length > 0) {
                vm.confirmR(vm.$t('Are you sure, you want to mark the selected status from Inactive to Active?'), true, false, function (result) {              
                    var url=`id=${val}&statusId=1002`
                    DataService.UpdateActiveInactiveAddGroup(url).then(response => {
                        if (response.data) {                            
                            vm.ShowAlert(vm.$t('StatusUpdated', [vm.$t('Group_Container')]), "Success", true, vm.$t('Alert'));
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');                         
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group_Container')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
        },
        DeleteContainer:function(){            
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group_Container")), function () {
                    var postJSON = {
                        ids: val,
                        tblname: "Talygen_asset_group",
                        primarycolname: "parent_asset_related_info_id"
                    };
                    vm.isLoading = true;
                    var poststing = JSON.stringify(postJSON);
                    DataService.DeleteItemsGroup(poststing).then(response => {
                        if (response.data) {
                           $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                           $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                           $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Group_Container')]), "Success", true, vm.$t('Alert'));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group_Container')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
        }

    },

}
</script>
